.header-title {

    color: $white;
    flex:1 1 100%;
    padding-top:3rem;
    padding-bottom:2rem;
    @include breakpoint-up(medium){    
        padding-top:4rem;
        padding-bottom:4rem;
    }

    h1 {
        text-transform: uppercase;
        font-size: 2rem;
        margin-bottom:0rem;
    }

    h2 {
        text-transform: capitalize;
        font-size:1.3rem;
        color:$dsblue;
        margin:0;
    }

    h3 {
        font-size:1.1rem;
        letter-spacing:1.2rem;
        text-transform: uppercase;
    }

}

.page-title {
    font-size:1.5rem;
    color:$midgrey;
    text-align: center;

    strong {
        color:$white;
    }

    .container {
        padding-top:2rem;
        padding-bottom:2rem;
    }

    span {
        transform:scale(1.5);
    }

}