.flex {
	display: flex;

	align-items: center;
	flex-direction: column;
	gap: 3rem;

	@include breakpoint-up(large) {
		flex-direction: row;
	}
}

.no-mobile-gap {
	@include breakpoint-down(medium) {
		gap: 0;
	}
}

.flex-wrap-3 {
	flex-wrap: wrap;

	>div {
		flex: 1 1 35% !important; 

		@include breakpoint-up(medium) {
			flex: 1 1 calc(40%) !important; 
		}
		@include breakpoint-up(large) {
			flex: 1 1 calc(20%) !important; 
		}
	}
}

.flex-wrap-4 {
	flex-wrap: wrap;
	flex-direction: row;

	position: relative;

	>div { 
		flex: 1 1 35% !important; 

		@include breakpoint-up(medium) {
			flex: 1 1 calc(40%) !important; 
		}
		@include breakpoint-up(large) {
			flex: 1 1 calc(20%) !important; 
		}
	}
}

.flex-top {
	align-items: flex-start;
}

.flex-str {
	align-items: stretch;
}

.no-gap {
	gap:0;
}

.col {
	flex-basis: 0;
	flex-grow: 1;
	flex-shrink: 1;
}

.col-50 {
	flex-basis: 50%;
	flex-grow: 0;
}

.shrink-15 {
	flex-shrink: 1.5;
}

.grow-15 {
	flex-grow: 1.5;
}

.grow-2 {
	flex-grow: 2;
}

.grow-3 {
	flex-grow: 3;
}