code {
	box-sizing: border-box;
	display: block;
	letter-spacing: 0;
	padding: 2rem;
	width: 100%;
	margin: 0;
}

pre {
	flex: 0 0 100%;
	overflow: auto;
	margin: 0;
	padding: 0;

	white-space: pre-wrap;
	/* Since CSS 2.1 */
	white-space: -moz-pre-wrap;
	/* Mozilla, since 1999 */
	white-space: -pre-wrap;
	/* Opera 4-6 */
	white-space: -o-pre-wrap;
	/* Opera 7 */
	word-wrap: break-word;

}

#c {
	position:absolute;
	top:0;
	left:0;
	width:100%;
	height:100%;
	transform: translateY(0px);
	background-color:rgba($color: #000000, $alpha: 0);
	pointer-events: none;;
}