.blog-entry {
    padding: 2rem;
    //box-shadow: 0 0 .5rem .1rem rgba($color: #000000, $alpha: 0.5);
   // background-color:$darkergrey;
   padding-top:3rem;
   padding-bottom:3rem;

    cursor:pointer;

    .blog-title {

        p {
            font-size:1.2rem;
            line-height:2;
        }
    }

    &:hover {
        .blog-featured img {
            transform:scale(1.035); 
            opacity:1;
        } 
    }

    .blog-featured{
        box-shadow: 0 0 1rem 0 rgba($color: #000000, $alpha: 0.5);

        img {
            transition-timing-function: ease-in-out;
            transition-duration: 0.15s;           
            opacity:0.75;
        }

        
    }
}

.header--breadcrumb {
	
    font-size:1rem;
    text-transform: uppercase;
    letter-spacing:0.5rem;
    display:flex;
	flex-direction: row;
    gap:0rem;
    justify-items:left;
    align-items: center;

    >* {
        flex: 1 1 0;
    }

    svg {
        fill:#ffffff;
    }
}